* {
  font: inherit;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

*, :before, :after {
  box-sizing: border-box;
}

ol, ul {
  list-style: none;
}

html, body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  text-rendering: optimizespeed;
  -webkit-font-smoothing: antialiased;
  line-height: 1.5;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

a:visited, a:link, a:active {
  color: currentColor;
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

article, aside, details, figcaption, footer, header, hgroup, menu, nav, section {
  display: block;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --clr-body-dark: #3b444e;
  --clr-body-light: #f5f7ff;
  --clr-body-darker: #e6e7ef;
  --ff: "Roboto", sans-serif;
  --padding-general-sides: 2rem;
  --margin-vert-between-sections: 3rem;
  --max-width-general: 1200px;
  --height-header-desktop: 140px;
  --nav-bar-height: 50px;
}

body {
  font-family: var(--ff);
  background-color: var(--clr-body-light);
  color: var(--clr-body-dark);
}

body h1, body h2 {
  font-weight: 600;
}

nav.main {
  z-index: 5;
  height: var(--nav-bar-height);
  background-color: #fff;
  border-top: .4px solid #2d343c;
  border-bottom: .4px solid #2d343c;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  overflow: scroll;
}

nav.main ul {
  width: 100vw;
  padding: 0 var(--padding-general-sides);
  height: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

nav.main ul .rechts {
  min-width: 20%;
  height: 100%;
  justify-content: space-between;
  display: flex;
}

nav.main ul li {
  width: max-content;
  height: 100%;
  align-items: center;
  padding: 0 1ch;
  display: flex;
}

nav.main ul li a {
  min-width: max-content;
  font-weight: 700;
}

nav.main ul li:hover {
  color: #fff;
  background-color: #2d343c;
}

nav.main ul > li {
  align-items: center;
  display: flex;
}

main.admin section.welcome {
  width: 100%;
  height: 100vh;
  text-align: center;
  border-bottom: .4px solid #2d343c;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

main.admin section.welcome h1 {
  padding: 0 0 0 var(--padding-general-sides);
  white-space: nowrap;
  letter-spacing: .05em;
  border-right: .15em solid #87cdfc;
  margin: 0 auto;
  font-family: Courier Prime, monospace;
  font-size: 120px;
  font-weight: 700;
  animation: 3.5s steps(30, end) typing, .5s step-end infinite blink-caret;
  overflow: hidden;
}

@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 80%;
  }
}

@keyframes blink-caret {
  from, to {
    border-color: #0000;
  }

  50% {
    border-color: #87cdfc;
  }
}

main.admin section.welcome .btns {
  width: 100%;
  padding: 0 var(--padding-general-sides);
  justify-content: center;
  column-gap: 5em;
  margin-top: .5em;
  display: flex;
}

main.admin section.welcome .btns a {
  background-color: #fff;
  border-radius: .5em;
  padding: 1em 2.5em;
  font-size: x-large;
  font-weight: 700;
  box-shadow: 10px 10px 15px #0000000d;
}

main.admin section.welcome .btns a:hover {
  transition: all .35s ease-out;
  transform: scale(1.05);
  box-shadow: 15px 15px 20px #0101010d;
}

main.admin section.products {
  min-height: calc(100vh - var(--nav-bar-height));
  position: relative;
}

main.admin section.products .product-sections {
  margin: var(--padding-general-sides);
}

main.admin section.products .product-sections table {
  width: 100%;
  margin-bottom: 2em;
  box-shadow: 0 0 20px #0000001a;
}

main.admin section.products .product-sections th, main.admin section.products .product-sections td {
  color: #000;
  text-align: left;
  vertical-align: middle;
  background-color: #fff3;
  padding: 10px;
}

main.admin section.products .product-sections thead {
  top: calc(var(--nav-bar-height)  - 2px);
  z-index: 4;
  position: sticky;
}

main.admin section.products .product-sections thead h2 {
  width: max-content;
  height: 0;
  text-align: end;
  position: absolute;
  top: 38px;
  left: -57px;
  transform: rotate(-90deg);
}

main.admin section.products .product-sections thead th {
  background-color: #679cc0;
  padding: 10px;
  font-weight: bold;
}

main.admin section.products .product-sections thead th:first-child {
  width: 5%;
}

main.admin section.products .product-sections thead th:nth-child(2) {
  width: 20%;
}

main.admin section.products .product-sections thead th:nth-child(3) {
  width: 10%;
}

main.admin section.products .product-sections thead th:nth-child(4), main.admin section.products .product-sections thead th:nth-child(5) {
  width: 15%;
}

main.admin section.products .product-sections thead th:nth-child(6) {
  width: 10%;
}

main.admin section.products .product-sections thead th:nth-child(7) {
  width: 5%;
}

main.admin section.products .product-sections thead th:nth-child(8) {
  width: 20%;
}

main.admin section.products .product-sections tbody .toggle {
  width: 25px;
}

main.admin section.products .product-sections tbody button:has(label) {
  cursor: default;
  background: #ddd;
  border: none;
  border-radius: 1em;
  outline: none;
  margin: .25em;
  padding: .4em .9em;
  font-size: .75rem;
}

main.admin section.products .product-sections tbody button:has(label) label {
  cursor: pointer;
}

main.admin section.products .product-sections tbody button:has(label:hover) {
  background: #c5c5c5;
  transition: transform 50ms;
  transform: scale(1.02);
}

main.admin section.products .product-sections tbody button:has(label.clicked) {
  background: #b5b5b5;
  transition: transform 50ms;
  transform: scale(1.02);
}

main.admin section.products .product-sections tbody .ratings {
  min-width: 91px;
}

main.admin section.products .product-sections tbody .ratings i {
  color: #adadad;
}

main.admin section.products .product-sections tbody .ratings i.yellow {
  color: #fdcc0d;
}

.hidden {
  display: none;
}

